<template>
  <div id="main-page">
    <div class="wrapper">
      <toolBar v-model="resolution" @events="toolEvent"></toolBar>
      <div id="draw-block" ref="drawBlock">
        <div id="drawLayer" :style="drawlayerStyle">
          <canvas ref="canvas" width="800" height="400"></canvas>
        </div>
        <div id="imageLayer" :style="imagelayerStyle">
          <vue-draggable-resizable
            :w="100"
            :h="100"
            :parent="true"
            v-for="(img, index) in imgList"
            :key="index"
          >
            <img :src="img.url" class="img-item" />
          </vue-draggable-resizable>
        </div>
      </div>
    </div>
    <trackBlock></trackBlock>

    <div id="github">
      <div class="mask"></div>
      <div class="icon">
        <a href="https://github.com/a1163675107/easy-animation"
          ><i class="fa fa-github" aria-hidden="true"></i
        ></a>
      </div>
    </div>
    <window
      v-if="isReview"
      :cssCode="cssCode"
      :canvas="this.$refs.canvas"
    ></window>
  </div>
</template>

<script>
import Layout from "@/layout/index";
import drawTools from "@/utils/draw.js";
import { mapState, mapGetters, mapMutations } from "vuex";
import {
  CreateImportCode,
  pointShake,
  sayManger,
  mergeData,
} from "@/utils/index.js";

export default {
  name: "",
  data() {
    return {
      resolution: [800, 400],
      image: null,
      layer: 1, //1 0
      imgList: null,
      isReview: false,
      cssCode: null,
      color:'rgba(0, 0, 0,1)'
    };
  },
  computed: {
    imagelayerStyle: function () {
      if (this.layer == 0) {
        return `z-index:20`;
      } else {
        return `z-index:10`;
      }
    },
    drawlayerStyle: function () {
      if (this.layer == 0) {
        return `z-index:10`;
      } else {
        return `z-index:20`;
      }
    },
    ...mapState(["tracksData", "trackTarget", "targets"]),
    ...mapGetters(["curData", "curTarget", "curTrack"]),
  },
  methods: {
    ...mapMutations(['setColor']) ,
    draw: function (command) {
      drawTools.draw(command);
    },
    importCode: function () {
      //
      CreateImportCode(this.resolution[0], this.resolution[1])
        .then((res) => {
          res = res.replace("||", "");
          this.$alert(res, "SUCCESS", {
            confirmButtonText: "",
          });
        })
        .catch((err) => {
          this.$alert(err, "ERROR", {
            confirmButtonText: "",
          });
        });
    },
    controlStep: function (mode) {
      //0 1
      let p = this.curTrack[this.curTarget];
      if (mode == 0) {
        if (p.data.length != 0) {
          p.cache.push(p.data.pop());
          if (p.finish == true) {
            //
            p.finish = false;
          }
        }
      } else {
        if (p.cache.length != 0) {
          let c = p.cache.pop();
          let o = p.data[0];
          p.data.push(c);
          c = c.split(",");
          o = o.split(",");
          if (Math.abs(o[0] - c[0]) < 6 && Math.abs(o[1] - c[1]) < 6) {
            //
            p.finish = true;
            sayManger.saySuccess.call(this);
          }
        }
      }
      this.draw();
    },
    preview: function () {

      if (this.isReview) {
        this.draw();
        this.isReview = false;
        return;
      }
      CreateImportCode(this.resolution[0], this.resolution[1])
        .then((res) => {
          this.draw('noColor');
          this.cssCode = res;
          this.isReview = true;
        })
        .catch((err) => {
          this.$alert(err, "ERROR", {
            confirmButtonText: "",
          });
        });
    },
    toolEvent: function (EventName, ...args) {
      let eventManger = {
        run: function () {
          this[EventName](...args);
        },
        importCode: this.importCode,
        insert: (imgList)=>this.imgList = imgList,
        choiceLayer: (i)=>this.layer = i,
        _changeColor:(color)=>this.color=color,
        controlStep: this.controlStep,
        preview: this.preview,

      };
      eventManger.run();
    },
  },
  mounted() {
    drawTools.getDom(this.$refs.canvas);
    let c = this.$refs.canvas;
    c.addEventListener(
      "click",
      (e)=>{
        if (e.target.className == "img-item") return; //||

        switch (this.layer) {
          case 0:
            return;
          case 1: {
            let target = this.curTarget;
            let p = this.curData; //
            p.cache = []; //
            //
            if (p.finish == true) return; //
            let command = null; //
            //
            let x = e.offsetX;
            let y = e.offsetY;
            p.data.push(x + "," + y); //
            //
            let originX = p.data[0].split(",")[0];
            let originY = p.data[0].split(",")[1];
            if (p.data.length == 1) {
              //
              command = "initial";
            } else if (Math.abs(originX - x) < 6 && Math.abs(originY - y) < 6) {
              //
              command = "close";
              sayManger.saySuccess.call(this);
              p.finish = true;
              p.data.pop();
              p.data.push(originX+','+originY)
            }
            this.draw(command); //
            break;
          }
          case 2: {
            let x = e.offsetX;
            let y = e.offsetY;

            let track = JSON.parse(JSON.stringify(this.curTrack))
              .filter((item) => item.finish == true && item.data.length != 0) //
              .map((item) => item.data);
            let result=drawTools.isInside(track,x,y);
            this.setColor({
              index:result,
              color:this.color
            })
            this.draw();
            break;
          }
        }
      },
      true
    );
    window.onresize = () => {
      let c = this.$refs.canvas;
      let db = this.$refs.drawBlock;
      let maxHeight = db.clientHeight - 15;
      let maxWidth = db.clientWidth - 15;
      let check = true;
      if (c.width * 1 >= maxWidth || c.height * 1 >= maxHeight) {
        db.style.display = "block";
        check = false;
      }
      if (check) {
        db.style.display = "flex";
      }
    };
  },
  watch: {
    resolution: function (val) {
      //
      let c = this.$refs.canvas;
      let db = this.$refs.drawBlock;
      let maxHeight = db.clientHeight - 15;
      let maxWidth = db.clientWidth - 15;

      if (val[0] * 1 >= maxWidth) {
        this.resolution[0] = maxWidth;
      }
      if (val[1] * 1 >= maxHeight) {
        this.resolution[1] = maxHeight;
      }
      c.style.width = this.resolution[0] + "px";
      c.style.height = this.resolution[1] + "px";
      c.width = this.resolution[0];
      c.height = this.resolution[1];
      //
      this.draw();
    },
  },
  components: {
    toolBar: Layout.toolBar,
    trackBlock: Layout.trackBlock,
    window: Layout.window,
  },
};
</script>

<style lang="scss" scoped>
#main-page {
  display: flex;
  flex-direction: column;
  position: relative;
  .wrapper {
    display: flex;
    #draw-block {
      flex-grow: 1;
      border: 1px solid white;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      overflow: auto;
      #drawLayer {
        background: rgba(0, 0, 0, 0);
        position: absolute;
        z-index: 20;
        canvas {
          border: 1px solid rgb(139, 139, 139);
        }
      }
      #imageLayer {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        z-index: 10;
        .vdr {
          border: 1px dashed rgba(255, 255, 255, 0.3);
        }
        .img-item {
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  #github {
    position: absolute;
    top: 0;
    right: 0;
    .mask {
      width: 5rem;
      height: 5rem;
      background: rgba(255, 255, 255, 0.3);
      clip-path: polygon(0 0, 100% 0, 100% 100%);
    }
    .icon {
      font-size: 2rem;
      position: absolute;
      top: 0.5rem;
      right: 0.5rem;
      z-index: 50;
      transition: 0.5s;
    }
    .icon:hover {
      transform: rotate(45deg);
    }
  }
}
</style>
